<template>
   <section id="customer">
      <span class="title">{{$t('service.service')}}</span>
      <el-col :span="24" style="padding-bottom: 0px" class="toolbar">
        <el-form :inline="true" :model="userForm">
            <el-form-item>
              <!-- @click="delGroup" -->
                 <el-button class="info"   @click="open(delGroup)" :disabled="sels.length === 0">{{$t('delete')}}</el-button>
            </el-form-item>
            <el-form-item>
                 <el-button class="warning" v-on:click="addCustomerShow = true">{{$t('newAdd')}}</el-button>
            </el-form-item>
        </el-form>
      </el-col>

      <el-table :data="userList"   v-loading="userLoading" style="width: aotu;" :row-class-name="tableRowClassName"  @selection-change="selsChange">
            <el-table-column type="selection" width="55" > </el-table-column>
            <el-table-column type="index" width="50"></el-table-column>
            <el-table-column prop="uid" :label="$t('service.thead[1]')">
            </el-table-column>
            <el-table-column prop="personName"  :label="$t('service.thead[2]')">
            </el-table-column>
            <el-table-column prop="contact"  :label="$t('service.thead[3]')">
            </el-table-column>
            <el-table-column  :label="$t('service.thead[4]')">
              <template slot-scope="scope">
                  <el-button  size="mini"  @click="showRoleInfoUnderUser(scope.$index,scope.row)">{{$t('allot')}}</el-button>
              </template>
            </el-table-column>
            <el-table-column :label="$t('service.thead[5]')">
                <template slot-scope="scope">
                  <el-button  size="mini"  @click="updateUserInfo(scope.$index,scope.row)">{{$t('xiuGai')}}</el-button>
                  <!-- <el-button  size="mini"  @click="deleteUsers(scope.$index,scope.row)">{{$t('delete')}}</el-button> -->
                  <el-button type="text" @click="open2(scope.$index,scope.row)">{{$t('delete')}}</el-button>
                </template>
            </el-table-column>
      </el-table>
      <!-- 新增 -->
      <el-dialog :title="$t('newCustomerServicer')" :visible.sync="addCustomerShow" id="addKeHu" center>
        <el-form :model="addCustomer"  :rules="rulesLogin" ref="addCustomer">
          <el-form-item :label="$t('Identification')" prop="username" :label-width="formLabelWidth">
            <el-input v-model="addCustomer.username" auto-complete="off"></el-input>
          </el-form-item>
          <el-form-item :label="$t('passWord')" prop="password" :label-width="formLabelWidth">
            <el-input v-model="addCustomer.password" auto-complete="off"></el-input>
          </el-form-item>
          <el-form-item :label="$t('service.thead[3]')" prop="contact" :label-width="formLabelWidth">
            <el-input v-model="addCustomer.contact" auto-complete="off"></el-input>
          </el-form-item>
          <el-form-item :label="$t('service.thead[2]')" prop="personName" :label-width="formLabelWidth">
            <el-input v-model="addCustomer.personName" auto-complete="off"></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="addCustomerShow = false" class="exit">{{$t('cancel')}}</el-button>
          <el-button type="primary" @click="createUser" class="sureSure">{{$t('confirm')}}</el-button>
        </div>
      </el-dialog>
      <!-- 分配 -->
      <el-dialog :title="$t('roleAssignment')" :visible.sync="roleShow" id="roleFenPei">
          <el-checkbox-group v-model="giveRole">
            <template v-for="(item,index) in allRole">
               <el-checkbox :label="item.authgroupId" @change="changeFun(item)" :key="index">{{item.authgroupName}}</el-checkbox>
            </template>
          </el-checkbox-group>
          <div slot="footer" class="dialog-footer">
              <el-button @click="roleShow = false" class="exit" >{{$t('cancel')}}</el-button>
              <el-button type="primary" @click="assignRoles4User" class="sureSure">{{$t('confirm')}}</el-button>
          </div>
      </el-dialog>
      <!-- 修改 -->
        <el-dialog :title="$t('power')" :visible.sync="limit" id="powerFrom">
          <el-form :model="limitDate" :rules="rulesLogin" ref="limitDate">
            <el-form-item :label="$t('Identification')" :label-width="formLabelWidth" prop="username">
              <el-input v-model="limitDate.username" auto-complete="off"></el-input>
            </el-form-item>
            <el-form-item :label="$t('passWord')" :label-width="formLabelWidth" prop="password">
              <el-input v-model="limitDate.password" auto-complete="off"></el-input>
            </el-form-item>
            <el-form-item :label="$t('service.thead[3]')" :label-width="formLabelWidth" prop="contact">
              <el-input v-model="limitDate.contact" auto-complete="off"></el-input>
            </el-form-item>
            <el-form-item :label = "$t('service.thead[2]')" :label-width="formLabelWidth" prop="personName">
              <el-input v-model="limitDate.personName" auto-complete="off"></el-input>
            </el-form-item>
          </el-form>
         <div slot="footer" class="dialog-footer" >
          <el-button @click="limit = false" class="exit" >{{$t('cancel')}}</el-button>
              <el-button type="primary" @click="updateUserInfoTrue" class="sureSure">{{$t('confirm')}}</el-button>
          </div>
        </el-dialog>
      <div class="page" v-show="total >10">
        <el-col :span="24" class="toolbar  ">
           <el-pagination
            background
            layout="prev, pager, next"
            :total="total" @current-change="handleCurrentChange">
          </el-pagination>
        </el-col>
      </div>
   </section>
</template>
<script>
import {ajax,dateStr2GmtTimeStamp,getDataYear} from '../../common/js/common.js'
export default {
   data(){
    var checkUser = (rule,value,callback) =>{
      /*  if(!value){
                    return callback(new Error("请输入用户名"))
                }else if(value.length<5 ){
                    return callback(new Error('用户名至少5位'))
                }else if(value.length>20 ){
                    return callback(new Error('用户名至多20位'))
                }else */
          if( !(/^[0-9a-zA-Z]{5,20}$/g.test(value))){
            return callback(new Error('5-20位字母数字'))
          }else{
            callback()
          }
    };
    var checkPass = (rule,value,callback) =>{
      if(!value){
        return callback(new Error("请输入密码"))
      }else if(value.length<5){
        return callback(new Error("密码至少5位"))
      }else if(value.length>20){
        return callback(new Error("密码至多20位"))
      }else{
        callback()
      }
    };
    var checkConcat = (rule,value,callback) =>{
      if(!value){
        return callback(new Error('请输入手机号'))
      }else if(!(/(^1[3|4|5|7|8]\d{9}$)|(^09\d{8}$)/).test(value)){
        return  callback(new Error('输入的手机格式不正确'))

      }else{
        callback()
      }
    };
    var checkName = (rule,value,callback) =>{
      if(!value){
        return callback('请输入姓名')
      }else{
        callback()
      }
    };
     return{
      
      sels: [],
      roleShow:false,
      giveRole:[],
      allRole:[],
      checkList:[],
      customerId:"",
      updateUserInfoData:{},
      addCustomerShow:false,
      addCustomer:{
        username:"",
        password:"",
        contact:"",
        personName:""
      },
       rulesLogin:{
                  username:[
                      {required: true, validator: checkUser,trigger:'blur'}
                  ],
                  password:[
                      {required: true,validator:checkPass,trigger:'blur'}
                  ],
                   contact:[
                      {required: true, validator: checkConcat,trigger:'blur'}
                  ],
                  personName:[
                      {required: true,validator:checkName,trigger:'blur'}
                  ]
              },
      total:0,
      limitDate:{
          uid:"",
          password:"",
          username:"",
          contact:"",
          personName:""
      },
       userForm:{
       },
       formLabelWidth:"120",
       userLoading:false,
       limit:false,
       userList:[],
       userInfoData:{
        page:1,
        rowNum:"10",
        // username:"",
        startTime:"",
        endTime:"",
       }

     }
   },
   methods:{

      open(fn) {
        this.$confirm('此操作将永久删除一批客服, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.$message({
            type: 'success',
            message: '删除成功!',
            callback:fn(),
          });
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          });          
        });
      },

     selsChange(sels) {  
            this.sels = sels ;
            console.log("这是sels");
            console.log(sels);

        },  
    delGroup() {  
            // var uids = this.sels.map(item => item.uid).join()//获取所有选中行的id组成的字符串，以逗号分隔 
            
            var uids=[];
                for(var i=0;i<this.sels.length;i++){
                 uids.push(this.sels[i].uid) 
                }

            console.log("这是uids");
            console.log(uids);
            let data={
          uids:uids
        };

        ajax(this,this.ApiConfig.userManagement.users.deleteUsers,data,(res) =>{
          if(res.code==200){
            console.log(res)
          this.getUserInfo();
        }else{
          this.$notify({
            title:"提示",
            message:res.message,
            type:"warning"
          })
          return false;
        }
          
        },"POST")
        
     }, 
     handleCurrentChange(row, event, column) {  
            this.$refs.table.toggleRowSelection(row)  
        }  , 
    isChecked:function(){
      alert("wawa ")
    },
     getUserInfo:function(){
        ajax(this,this.ApiConfig.userManagement.users.queryUserInfo,this.userInfoData,(res) =>{

            if(res.code==200){
                       console.log(res)
                    this.userList = res.data.rows;
                    this.total = res.data.total;
                    }else{
                      this.$notify({
                        title:"提示",
                        message:res.message,
                        type:"warning"
                      })
                      return false;
                    }

      },"POST")
     },
     // 创建角色
      createUser:function(){
        
 this.$refs.addCustomer.validate((valid) => {
            if(valid){
        ajax(this,this.ApiConfig.userManagement.users.createUser,this.addCustomer,(res) =>{
            if(res.code==200){
               this.addCustomerShow = false;
                  console.log(res)
                  this.addCustomer.username="";
                  this.addCustomer.password="";
                  this.addCustomer.contact="";
                  this.addCustomer.personName="";
                  this.getUserInfo()
                }else{
                  this.$notify({
                    title:"提示",
                    message:res.message,
                    type:"warning"
                  })
                  return false;
                }
        },"POST")
         
         }else {
            return false;
            }
    })
      },
      open2:function(index,row) {
        this.$confirm('此操作将永久删除该客服, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.deleteUsers(index,row);
          this.$message({
            type: 'success',
            message: '删除成功!'
          });
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          });          
        });
      },
      // 删除角色
      deleteUsers:function(index,row){
        let uids =[] ;
        uids.push(row.uid);
        // console.log(row)
        // console.log(uids)
        let data={
          uids:uids
        };
        ajax(this,this.ApiConfig.userManagement.users.deleteUsers,data,(res) =>{

        if(res.code==200){
          console.log(res)
          this.getUserInfo();
        }else{
            this.$notify({
              title:"提示",
              message:res.message,
              type:"warning"
            })
            return false;
          }
        },"POST")

      },
      // 修改角色信息参数获取
      updateUserInfo:function(index,row){
          this.limit =true;
          this.limitDate.uid = row.uid;
          this.limitDate.username = row.username;
          this.limitDate.password = row.password;
          this.limitDate.contact = row.contact;
          this.limitDate.personName = row.personName;
      },
      //发起请求  修改信息
      updateUserInfoTrue:function(){
 this.$refs.limitDate.validate((valid) => {
            if(valid){       
         ajax(this,this.ApiConfig.userManagement.users.updateUserInfo,this.limitDate,(res) =>{
            if(res.code==200){
                   console.log(res);
                    this.limit=false;
                    this.getUserInfo()
                  }else{
                      this.$notify({
                        title:"提示",
                        message:res.message,
                        type:"warning"
                      })
                      return false;
                    }
         },"POST")

         }else {
            return false;
            }
    })
      },
      // 查看用户下角色信息
      showRoleInfoUnderUser:function(index,row){
         this.giveRole=[];
        let data = {
          uid:row.uid,
        };
        this.customerId=row.uid;
        ajax(this,this.ApiConfig.userManagement.users.showRoleInfoUnderUser,data,(res) =>{
              if(res.code == 200){

                console.log(res)
                this.allRole = res.data;
                this.roleShow = true;
                console.log(this.allRole)
                let arr = this.allRole;
                arr.forEach((item,i,arr) =>{
                      if(this.allRole[i].isSelected ==1){
                        this.giveRole.push(this.allRole[i].authgroupId)
                      }
                })
              }else{
                this.$notify({
                  title:"提示",
                  message:res.message,
                  type:"warning"
                });
                return false;
              }
        },"POST")
      },
      //选择权限(isSelected,authId,authName)
      changeFun:function(target) {
        if(target.isSelected ==0){
          target.isSelected =1;
        }else{
          target.isSelected =0;
        }
        console.log(target.authgroupId)
        if(target.authgroupId==1){
          this.allRole[0]=target;
        }else if(target.authgroupId==2){
             this.allRole[1]=target;
          }else{
            this.allRole[target.authgroupId] =target
          }
    },
      // 客服分配角色

     assignRoles4User:function(){       //  点击确定做的事情
         this.checkList = [];
         console.log(this.checkList+"这是前面的checkList")
      for(var i=0;i<this.allRole.length;i++){
          if(this.allRole[i].isSelected==1){
            this.checkList.push(this.allRole[i].authgroupId)    //选中的列表点击确定之前
          }
         }
         console.log(this.checkList+"这是checkList")
      let data = {
            uid:this.customerId,
            authgroupIds:this.checkList
      };
      console.log(data.roleId)
        ajax(this,this.ApiConfig.userManagement.users.assignRoles4User,data,(res) =>{ 
          if(res.code ==200){
            console.log(res)
             this.$notify({
              title:"提示",
              message:res.message,
              type:'success'
            })
            this.roleShow = false;
          }else{
            this.$notify({
              title:"提示",
              message:res.message,
              type:'warning'
            })
            return false; 
          }
        
      },"POST")
     },
     tableRowClassName:function({row, rowIndex}){
         if(rowIndex % 2 == 1){
            return "success-row"
         }
     },
       handleCurrentChange:function(val){
       this.acountDate.page = val;
       this.queryFrontUserAccount();
     },
   },
   mounted:function(){
    this.getUserInfo();
   }
}
</script>

<style scoped lang="postcss"> 
.toolbar .el-form-item .warning{
  /* width: 100px; */
  height: 30px;
}
.exit{
  width: 158px;
  background-color: #e7e7e7;
}
.sureSure{
  width: 158px;
}
.el-form--inline{
  /* margin-right: 75px; */
  display: flex;
  justify-content: space-between ;

}
#customer .el-checkbox{
      width: 90%;
    }
    #addKeHu{
      .el-button--primary{
        background-color: #bf9764;
        border-color: #bf9764;
      }
    }
    /* .exit{
      width: 150px;
      background-color: #e7e7e7;
    }
    .sureSure{
      width: 150px;
    } */
    
</style>
<style>
#customer {

      .el-dialog{
        width: 400px;
        padding: 0 10px;
      }
      .el-form-item__error{
        left:250px;
      }
      
      
      .el-form-item{
        margin-bottom: 0;
      }
      .el-dialog__title{
        color: #bf9864;
        height: 76px;
        vertical-align: top;
      }
      .el-form-item__label{
        line-height: 30px;
      }

      .el-dialog--center .el-dialog__body{
        padding: 0px 25px 10px;

      }
      .el-checkbox__input.is-checked+.el-checkbox__label{
        color:#bf9864;
      }
      .el-checkbox__input.is-checked .el-checkbox__inner, .el-checkbox__input.is-indeterminate .el-checkbox__inner{
        background-color: #bf9864;
        border-color: #bf9864;
      }
      .el-button--primary{
        background-color: #bf9864;
        border-color: #bf9864;
      }
      #roleFenPei{
        .el-dialog__title{
          color: #000;
          font-size: 16px;
        }
        .el-dialog__body{
          height: 390px;
          overflow-y: auto;
          padding-top: 1px;
        }
      }
      #powerFrom{
        .el-dialog__header{
          padding-bottom: 0;
        }
        .el-dialog__title{
          color: #000;
          font-size: 16px;
        }
        .el-dialog__body{
          padding-top: 1px;
          padding-bottom: 1px;
        }
      }
    }
</style>





